/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from "@emotion/react";
import styled from "@emotion/styled";

const TitleContainer = styled.div`
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
  border-left: 3px solid #08979c;
  border-radius: 3px;
  margin-top: 40px;
  margin-bottom: 20px;
  color: #006d75;
  background: #e6fffb;
  padding: 5px 10px;
`;

const defaultTitle = {
  flex: "🤔 我有问题！",
  who: "Who? 🕵🏻 你是谁? 🕵",
  where: "Where? 🕵🏻 你打哪来? 🕵",
  what: "What? 🕵🏻 你要做什么? 🕵",
  ans: "✋ ",
};

const renderPrefix = (children, type) => {
  if (type === "none") return null;
  if (type === "ans") return defaultTitle[type];
  if (children) return "🤔 ";
  return null;
};

const QuestionTitle = ({ children, type = "flex" }) => (
  <TitleContainer>
    {renderPrefix(children, type)}
    <span>{children || defaultTitle[type]}</span>
  </TitleContainer>
);

export default QuestionTitle;
